@import "../../../style-config/variable.scss";
@import "../../../style-config/mixins.scss";

.call-to-action{
    height:47px;
    width:fit-content;
    padding: 0 15px;
    background:$white;
    border-radius: 6px;
    display:flex;
    align-items:center;
    justify-content:center;
    color: black;
    cursor: pointer;
    position:relative;
    overflow: hidden;
    @include typography(18px,bold,1);
    .text,.arrow-icon{
        position:relative;
        z-index:1;
        transition: all ease 500ms
    }
    &:before{
        content:"";
        position: absolute;
        left : 0;
        width:100%;
        height: 100%;
        background:$yellow;
        z-index: 0;
        transform: translateX(-100%);
        transition: all 300ms cubic-bezier(0.55,0.055,0.675,0.19)
    }
    &:hover{
        &:before{
            transform:translateX(0)
        }
        .arrow-icon{
            background: $white;
        }
        svg{
            transform:rotate(-45deg);
        }
    }


}